// VARS / CONFIG
$download_meta-width:   18rem !default;
$download_break-point:  $small-width !default;
$download_border-width: $border-width-small !default;

.list--downloads {
    .list__item {
        @include respond-max($download_break-point) {
            margin-bottom: $slug-large;
        }
    }
}

.download {
    border-bottom: $download_border-width solid $color-disabled;
    display: block;
}

.download__link {
    display: block;

    @include respond-min($download_break-point) {
        @include linkUnderlineReset;
        @include linkUnderline('.download__heading-inner');
    }

    &:hover,
    &:focus,
    &:active {
        .download__cta {
            background-color: lighten($color-secondary, 5%);
            border-color: $color-white;
            color: $color-white;
            text-decoration: none;
        }
    }
}

.download__heading {
    display: block;
    font-size: $zeta;
    margin: 0;
    padding: 0 0 $space-half;

    @include respond-min($download_break-point) {
        display: inline-block;
        font-size: $normalis;
        padding-right: $space-half;
        padding-top: $space-half;
        vertical-align: bottom;
        width: calc(100% - #{$download_meta-width});
    }
}

.download__meta-list {
    @extend .list;
    @extend %cf;

    margin: 0;
    padding: 0;

    @include respond-min($download_break-point) {
        display: inline-block;
        text-align: right;
        vertical-align: bottom;
        width: $download_meta-width;
    }
}

.download__meta {
    background-color: $color-off-white;
    display: inline-block;
    margin: $space-half 0 $space-half $space-half;
    padding: 0 $space-sixth;
    vertical-align: bottom;

    @include respond-min($download_break-point) {
        font-size: $milli;
        margin-top: ($space-half / ($milli)) * 1rem;
    }
}

.download__meta--type {
    @include respond-min($download_break-point) {
        text-align: center;
        width: 3.5rem; // the width of "DOCX" with a bit of breathing space
    }
}

.download__meta--link {
    background-color: transparent;
    float: left;
    margin: 0;
    padding: 0;

    @include respond-min($download_break-point) {
        float: none;
        font-size: $normalis;
        margin-left: $space-half;
    }
}

.download__cta {
    background-color: $color-secondary;
    border-bottom: $download_border-width solid mix($color-secondary, $color-black, 80%);
    display: block;
    margin: 0 0 $download_border-width * -1;
    padding: $space-half;

    &,
    &:visited {
        color: $color-white;
    }
}

.download__cta-text {
    display: inline-block;
    margin-right: $gutter-small;
    vertical-align: middle;

    @include respond-min($download_break-point) {
        display: none;
    }
}

.download__cta-icon {
    display: inline-block;
    vertical-align: middle;
}
