 * Hide only visually, but have it available for screenreaders
 * as per: http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
*/
.visually-hidden,
.icon-fallback,
.js .form__field--upload,
.js .form__field--image-upload,
.list--back-to-top .list__link:not(:hover):not(:active):not(:focus),
.list--skip-links .list__link:not(:hover):not(:active):not(:focus),
.home-page .breadcrumb,
.breadcrumb__prefix {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.visually-hidden-reset {
  clip: auto;
  height: auto;
  margin: auto;
  overflow: visible;
  padding: 0;
  position: static;
  width: auto;
}

.visually-hidden-full {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.desktop-hidden {
  display: none;
}

.tablet-hidden {
  display: none;
}
