$hero-image-height:           pxToRem(800px);
$hero-image-height-small:     pxToRem(500px);

.hero {
    position: relative;

    .hero__carousel.ready {
        .hero__meta,
        .hero__button {
            @include respond-min($large-width) {
                opacity: 0;
            }
        }

        .hero__heading,
        .hero__subheading {
            @include respond-min($large-width) {
                transform: translateX($space-and-quarter * 2);
            }
        }
    }

    .hero__slide {
        &.is-selected {
            .hero__heading,
            .hero__subheading {
                transform: translateX(0);
            }

            .hero__meta,
            .hero__button {
                opacity: 1;
            }
        }
    }

    .hero__image-container {
        height: $hero-image-height-small;
        overflow: hidden;
        position: relative;

        @include respond-min($large-width) {
            height: $hero-image-height;
        }

        &::before {
            background-image: $gradient;
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;

            @include respond-max($small-mid-width) {
                background-image: $gradient-reverse;
            }
        }

        &.no-content {
            &::before {
                @include respond-max($small-mid-width) {
                    background-image: $gradient;
                }
            }
        }

        &.compat-object-fit {
            background-position: center center;
            background-size: cover;

            .hero__image {
                opacity: 0;
            }
        }
    }

    .hero__image {
        height: 100%;
        object-fit: cover;
        width: 100%;
    }

    .hero__content {
        color: $color-white;
        left: 50%;
        padding: 0 $space-double;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        z-index: 1;

        @include respond-min($mid-width) {
            padding: 0 $space-triple;
            text-align: center;
        }

        @include respond-max($small-mid-width) {
            bottom: $space-and-third * 2;
            left: 0;
            padding: 0 $space-two-thirds;
            top: auto;
            transform: none;
        }
    }

    .hero__meta {
        @extend %micro-meta;

        display: inline-block;
        transition: opacity (($transition-speed-slow * 2) + $transition-speed-fast) ease ($transition-speed-slow * 3);
    }

    .hero__heading {
        @extend %kilo;

        display: block;
        font-family: $font-family-gilroy;
        letter-spacing: $border-width-mid * 2;
        margin-top: $space-two-thirds;
        text-transform: uppercase;
        transition: transform ($transition-speed-slow + $transition-speed-fast) ease $transition-speed-slow;

        @include respond-max($large-width) {
            line-height: 1.1;
        }

        @include respond-max($small-width) {
            font-size: $kilo-small;
        }
    }

    .hero__subheading {
        @extend %kilo;
        @extend %subheading;

        color: $color-white;
        display: block;
        line-height: 1.1;
        transition: transform ($transition-speed-slow * 2) ease $transition-speed-slow;

        @include respond-max($large-width) {
            font-size: $alpha;
        }
    }

    .hero__button {
        border: $border-width-small solid $color-white;
        margin: $space-two-thirds 0 0;
        transition: opacity ($transition-speed-slow * 2) ease ($transition-speed-slow * 5);
    }

    .flickity-buttons-wrapper {
        @include respond-max($small-mid-width) {
            bottom: $space-and-sixth;
            top: auto;
        }
    }

    .flickity-page-dots-wrapper {
        @include respond-min($full-width) {
            bottom: $space-triple + $space-hard;
        }
    }
}
