// Announcements and Tracked URLs
.site-announcement {
    background-color: $color-subtle;

    @include respond-max($x-large-width) {
        border-bottom: $border-width-small solid $color-black;
    }

    .container {
        max-width: $container-standard;
    }

    .alert {
        border: 0;
        margin: 0;

        @include respond-max($x-large-width) {
            padding-left: 0;
            padding-right: 0;

            &::before {
                content: none;
            }
        }

        &::before {
            left: 0;
        }

        .home-page &:first-child {
            margin-top: 0;
        }
    }
}
