.property-list {
    background-color: $color-grey;
    background-image: url('../dist/images/taupe-background.svg');
    background-position: right top;
    background-repeat: no-repeat;
    background-size: percentage(9 / 10);
    padding: ($space-double + $space-twelfth) 0 ($space-and-quarter * 2);

    @include respond-min($large-width) {
        padding: ($space-and-third + $space-and-third) 0 ($space-quadruple + $space);
    }

    @include respond-min($full-width) {
        background-size: auto;
    }

    .property-list__container {
        margin-top: $space;
    }

    .property-list__listing {
        &::after {
            background-color: $color-white;
            bottom: 0;
            box-shadow: $shadow;
            content: '';
            height: 50%;
            left: $shadow-position;
            position: absolute;
            right: $shadow-position;
            z-index: -1;
        }

        &:nth-child(even) {
            .property-list__content {
                order: -1;
            }
        }

        &:not(:first-child) {
            margin-top: $space-and-quarter;

            @include respond-min($large-width) {
                margin-top: ($space-and-quarter * 2);
            }
        }
    }

    .property-list__meta {
        @extend %micro-meta;

        color: $color-white;
        margin: 0 0 ($space-hard * 2);

        @include respond-min($large-width) {
            margin-left: $space-and-quarter;
        }
    }

    .property-list__image-container {
        @include respond-min($x-large-width) {
            width: 50%;
        }
    }

    .property-list__content {
        background-color: $color-white;

        @include respond-max($large-width) {
            padding: $space-hard * 2;
        }
    }

    .property-list__heading {
        @extend %h2;
    }

    .property-list__subheading {
        @extend %subheading;
        @extend %zeta;

        display: block;
        margin: $border-width-large 0 0;

        @include respond-max($large-width) {
            font-size: $theta;
        }
    }

    .property-list__intro {
        color: $color-black;
        font-size: $eta;
        font-weight: $font-weight-normal;
        margin-top: $space;
    }

    .property-list__text {
        margin-bottom: $space-and-third;
    }
}
