$circle-large-image-size:     pxToRem(551px);
$circle-large-image-offset:   pxToRem(212px);
$circle-large-image-offset-y: pxToRem(115px);
$circle-medium-image-size:    pxToRem(196px);
$circle-medium-image-offset:  pxToRem(314px);
$circle-small-image-size:     pxToRem(120px);
$circle-small-image-offset:   pxToRem(91px);
$circle-container-height:     pxToRem(630px);

.content-circles {
    .container {
        @include respond-min($mid-width) {
            display: flex;
            min-height: $circle-container-height;
        }
    }

    .content-circles__col {
        padding: ($space-and-quarter * 2) 0;
        position: relative;

        @include respond-min($mid-width) {
            padding: ($space-quadruple + $space) 0;
            width: 50%;
        }

        &:first-child {
            @include respond-max($mid-width) {
                padding-bottom: 0;
            }
        }
    }

    .content-circles__heading {
        margin-top: -$border-width-x-large;
    }

    .content-circles__content {
        padding-top: $space;
    }

    .content-circles__button {
        margin: $space-and-half 0 0;
    }

    .content-circles__image {
        border: $border-width-x-large solid $color-white;
        border-radius: 50%;

        &.image-small {
            left: 0;
            position: absolute;
            top: $space-double;
            width: $circle-small-image-size;

            @include respond-min($mid-width) {
                left: $space-quadruple * 2.5;
            }

            @include respond-min($xx-large-width) {
                left: auto;
                right: $circle-small-image-offset;
                top: -$space;
            }
        }

        &.image-medium {
            bottom: $space-double;
            left: $circle-medium-image-offset + $space-quadruple;
            position: absolute;
            width: $circle-medium-image-size;

            @include respond-min($mid-width) {
                bottom: auto;
                left: $space;
            }

            @include respond-min($xx-large-width) {
                left: auto;
                right: $circle-medium-image-offset;
                top: $space-and-third;
            }

            @include respond-max($small-width) {
                display: none;
            }
        }

        &.image-large {
            width: $circle-large-image-size;

            @include respond-min($mid-width) {
                left: $space;
                max-width: $circle-large-image-size;
                position: absolute;
                top: $circle-large-image-offset-y;
            }

            @include respond-min($large-width) {
                left: auto;
                right: -$circle-large-image-offset / 1.5;
            }

            @include respond-min($xx-large-width) {
                right: -$circle-large-image-offset;
            }

            @include respond-max($large-width) {
                margin-bottom: -$border-width-x-large;
            }
        }
    }
}
