// Container
%container,
.container {
    @extend %cf;

    @include container;

    padding: 0 $space-two-thirds;

    @include respond-min($x-small-width) {
        padding: 0 $space-and-quarter;
    }
}

.container--narrow {
    max-width: $container-narrow;
}

%container--flush,
.container--flush {
    padding-left: 0;
    padding-right: 0;
}

.container {
    .container {
        @extend %container--flush;
    }
}

.container--wide {
    max-width: $container-wide;
}

.outer-container {
    @extend %container;

    .modular & {
        max-width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
}

.measure {
    max-width: $content-max-width;
}
