// Header
//
// This file is intentially sparse and are intended as placeholder for your header styles
// See sass/layout/_header.scss.
//
// Styleguide 18.2
$identity-triangle-height: 90px;
$identity-triangle-height-large: 106px;
$identity-triangle-width: 84px;
$identity-triangle-width-large: 90px;

.site-header {
    background-color: $color-grey-lighter;
    margin-top: 0;
    padding: 0;
    position: relative;
    transition: background-color $transition-speed ease-out;
    z-index: zIndex('site-header');

    @include respond-min($mid-width) {
        flex: 0 0 auto;
    }

    .no-js & {
        padding-bottom: 0;
    }

    .menu-is-open & {
        background-color: $color-black;
    }

    .hero-first & {
        background-color: $color-black;

        @include respond-min($large-width) {
            background-color: transparent;
        }

        .js-menu-toggle {
            background-color: $color-black;

            &:hover,
            &:focus,
            &:active {
                background-color: $color-white;

                .toggle-icon {
                    &::before,
                    &::after,
                    .toggle-icon__line {
                        background-color: $color-black;
                    }
                }
            }

            .toggle-icon {
                &::before,
                &::after,
                .toggle-icon__line {
                    background-color: $color-white;
                }
            }
        }

        .nav--primary-navigation {
            .list__link {
                &,
                &:visited {
                    color: $color-white;
                }
            }
        }
    }

    > .container {
        display: flex;
        justify-content: space-between;
        max-width: $container-standard;
        position: relative;
        z-index: 1;

        @include respond-min($large-width) {
            height: $header-height;
        }

        &::after {
            content: none;
        }
    }

    .identity {
        background-color: $color-white;
        height: $identity-triangle-height-large;
        padding: ($space-two-thirds + $space-sixth) $space-double ($space-two-thirds + $space-sixth) 0;
        position: relative;

        @include respond-max($small-width) {
            height: $identity-triangle-height;
            padding-right: $space-two-thirds;
        }

        &::before {
            background-color: $color-white;
            border-bottom-right-radius: $border-width-mid;
            content: '';
            height: 100%;
            position: absolute;
            right: -$border-width-small;
            top: 0;
            width: 100vh;
            z-index: -1;
        }

        &::after {
            border-color: $color-white transparent transparent;
            border-style: solid;
            border-width: $identity-triangle-height-large $identity-triangle-width-large 0 0;
            content: '';
            height: 0;
            position: absolute;
            right: -$identity-triangle-width-large;
            top: 0;
            width: 0;

            @include respond-max($small-width) {
                border-width: $identity-triangle-height $identity-triangle-width 0 0;
                right: -$identity-triangle-width;
            }
        }
    }

    .js-menu-toggle {
        background-color: $color-grey-lighter;
        border-radius: 0;
        padding: $space-two-thirds;
        transform: translateX($space-two-thirds);

        &:hover,
        &:focus,
        &:active {
            background-color: $color-black;

            .toggle-icon {
                &::before,
                &::after,
                .toggle-icon__line {
                    background-color: $color-white;
                }
            }
        }

        .icon {
            height: pxToRem(22px);
            margin-right: 0;
            width: pxToRem(26px);
        }

        .toggle-icon {
            &::before,
            &::after,
            .toggle-icon__line {
                background-color: $color-black;
                transition: background-color $transition-speed-slow ease-in-out;
            }
        }
    }
}

.site-header__top-nav {
    background-color: $color-black;

    @include respond-max($large-width) {
        display: none;
    }

    .container {
        display: flex;
        max-width: $container-standard;
        padding-bottom: $space;
        padding-top: $space;
    }

    .list {
        line-height: 1;
    }

    .list__item {
        margin-right: $space-double + $space-third;
        margin-top: 0;
    }

    .list__link {
        font-size: $milli;

        &,
        &:visited {
            color: $color-white;
            text-decoration: none;
        }

        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;
        }
    }
}

.site-header__top-nav-heading {
    @extend %subheading;

    color: $color-white;
    display: inline-block;
    flex-grow: 1;
    line-height: 1.2;
}
