// Tables
//
// Why have table styles? Given the widespread use of tables in plugins like calendars and date pickers, we've opted to isolate our custom table styles.
// Table modifiers can be combined as normal, for example you mighr like to have a compact and striped table with borders.
//
// ##See sass/objects/_tables.scss.
//
// Markup:
// <table class="data-table {$modifiers}">
//  <caption>Table Caption</caption>
//  <thead>
//      <tr>
//          <th scope="col">Question</th>
//                  <th scope="col">Answer</th>
//      </tr>
//  </thead>
//  <tbody>
//      <tr>
//          <td>Time?</td>
//          <td>No Time</td>
//      </tr>
//      <tr>
//          <td>Yes?</td>
//          <td>No</td>
//      </tr>
//      <tr>
//          <td>Sollicitudin</td>
//          <td>Malesuada</td>
//      </tr>
//  </tbody>
// </table>
//
// .data-table--compact     - Reduces padding
// .data-table--striped     - Adds stripes
// .data-table--bordered    - Adds borders
//
// Styleguide 17

$table-caption-side: top !default;

.data-table,
%data-table {
    width: 100%;

    @if $table-caption-side == 'bottom' {
        caption-side: bottom;
    }

    caption {
        @extend %data-table__caption;
    }

    thead {
    }

    tbody {
        tr:first-child {
            td {
                padding-top: $space-half;
            }
        }
    }

    tfoot {
    }

    tr {
    }

    th {
        @extend %milli;

        background-color: $color-primary;
        border: $border-width-small solid $color-primary;
        border-bottom: 0;
        color: $color-primary-offset;
        font-weight: $font-weight-heading;
        padding: $space-half;
        text-align: left;
    }

    td {
        @extend %milli;

        border: $border-width-small solid $color-primary;
        padding: $space-half;
    }

    .button {
        &,
        .editor & {
            margin: $space-quarter;

            @include respond-max($x-small-width) {
                margin: 0;

                + .button {
                    margin-top: $space-half;
                }
            }
        }
    }
}

table {
    .page-content &:not([class]) {
        @extend %data-table;
    }
}

%data-table__caption,
.data-table__caption {
    @if $table-caption-side == 'bottom' {
        margin-top: $slug-small;
    }
    @else {
        margin-bottom: $slug-small;
    }
}
