// ========================================================
//  Content
// ========================================================
//  This file is intentionally sparse and are intended as
//  placeholder for your content styles that aren’t covered
//  by the objects in /sass/objects/. Orbit contains the
//  following content styles as default, feel free to
//  change as needed.
$header-height: pxToRem(105px);

.site {
    min-height: 100vh;
    overflow-x: hidden;
    position: relative;

    @include respond-min($mid-width) {
        display: flex;
        flex-direction: column;
    }

    @include if-ie(ie) {
        display: block;
    }
}

.site-main {
    margin: $space-triple 0 $space-double;
    z-index: zIndex('site-main');

    @include respond-min($mid-width) {
        flex: 1 0 auto;
    }

    @include respond-max($large-width) {
        margin-top: $space-and-quarter;
    }

    .hero-first & {
        margin-top: 0;

        @include respond-min($large-width) {
            margin-top: -$header-height;
        }
    }

    .homepage-modular &,
    .index-modular & {
        margin-bottom: 0;
    }
}

.site-content {
    @extend %cf;

    .one-column & {
        margin: 0 auto;
    }
}

.page-content {
    @extend %cf;

    .two-column & {
        margin-bottom: $slug;

        @include respond-min($mid-width) {
            float: left;
            margin-bottom: 0;
            padding-right: $gutter-large;
            width: percentage(8.75 / 12);
        }
    }
}

.site-content__intro {
    @extend %cf;

    color: $color-black;
    font-size: $iota;
    position: relative;

    @include respond-max($large-width) {
        margin-bottom: $space-and-quarter;
    }

    &::before {
        background-color: $color-grey-lighter;
        content: '';
        height: calc(100% + #{$space-triple});
        left: -100vw;
        position: absolute;
        top: -$space-triple;
        width: 200vw;
        z-index: -1;

        @include respond-max($large-width) {
            height: calc(100% + #{$space-and-quarter * 2});
            top: -$space-and-quarter;
        }
    }

    .site-content__intro-container {
        background-color: $color-white;
        padding: ($space-double + $space-sixth) ($space-and-quarter * 2);
        position: relative;

        @include respond-max($large-width) {
            padding: $space-and-quarter;

            &::before {
                background-color: $color-white;
                content: '';
                height: 100%;
                position: absolute;
                right: -$space-and-quarter;
                top: 0;
                width: $space-and-quarter;
            }
        }
    }

    .news-list & {
        margin-bottom: $space-and-quarter * 2;

        @include respond-max($large-width) {
            margin-bottom: ($space-and-quarter * 2);
        }

        &::before {
            @include respond-min($large-width) {
                height: ($space-and-quarter * 2) + $space-triple;
            }
        }
    }
}

.site-content__intro-container {
    @extend %cf;
}

.news-list__col {
    @include respond-min($large-width) {
        float: left;
        width: percentage(2 / 5);

        &:last-child {
            width: percentage(3 / 5);
        }
    }

    @include respond-max($large-width) {
        &:last-child {
            margin-top: $space-two-thirds + $space-sixth;
        }
    }
}
