%form--search,
.form--search {
    .form__append-group {
        flex-wrap: nowrap;
    }

    .form__field {
        flex: 1 1 auto;
        min-height: $space-and-third;
        min-width: 1px;
        width: auto;
    }

    .button {
        margin: 0;
        min-height: $space-and-third;
    }

    .icon {
        display: none;
    }

    .icon-fallback {
        @include visuallyhiddenreset;
    }

    .site-search__focus-button {
        display: none;
    }
}
