// ========================================================
//  Brand palette
// ===============

//  Primary palette
// =================
$primary-palette: (
    'blue':                     #49758d,
    'blue-light':               #7ba6bb,
    'red':                      #ff4549
);

//  Neutral palette
// =================
$color-black:                   #111821;
$color-black-light:             #2c3239;
$color-grey-dark:               #666;
$color-grey:                    #81736e;
$color-grey-mid:                #ddd;
$color-grey-light:              #eee;
$color-grey-lighter:            #f5f5f5;
$color-off-white:               #f9f9f9;
$color-white:                   #fff;

// ========================================================


// ========================================================
//  J∆DU brand palette
// ====================
$color-jadu-blue:               #95bed9;
$color-jadu-blue-dark:          #2080c0;
$color-spacecraft-yellow:       #ffe807;

// ========================================================


// ========================================================
//  Non-brand palette
// ===================

//  UI palette
// ============
//  These can updated to use colours from the `Primary palette`
$ui-palette: (
    'blue':                    #226fb4,
    'blue-dark':               #095294,
    'green':                   #a4d0b1,
    'orange':                  #f5a623,
    'purple':                  #6d2077,
    'red':                     #f3a3a9,
    'yellow':                  #ffe13b
);

// ========================================================




// ========================================================
//  Site Colours
// ==============

//  Primary
// =========
$color-primary:                 map-get($primary-palette, 'blue');
$color-primary-light:           lighten($color-primary, 20%);
$color-primary-offset:          $color-white;

//  Text
// ======
$color-text-default:            $color-grey-dark;
$color-text-heading:            $color-primary;
$color-text-muted:              $color-grey;
$color-text-disabled:           $color-grey-light;

//  Secondary
// ===========
$color-secondary:               map-get($primary-palette, 'blue');
$color-secondary-dark:          darken($color-secondary, 10%);
$color-secondary-offset:        $color-white;

//  Tertiary
// ===========
$color-tertiary:                map-get($primary-palette, 'blue-light');
$color-tertiary-offset:         $color-white;

//  Response
// ==========
$color-success:                 map-get($ui-palette, 'green');
$color-warning:                 map-get($ui-palette, 'orange');
$color-danger:                  map-get($ui-palette, 'red');
$color-danger-text:             darken($color-danger, 40%);
$color-disabled:                $color-grey-light;

//  Stand-out
// ===========
$color-standout:                $color-black;
$color-standout-offset:         $color-white;

//  Link
// ======
$color-link:                    $color-black;
$color-link-visited:            $color-grey-dark;
$color-link-hover:              map-get($primary-palette, 'blue');
$color-link-focus:              map-get($primary-palette, 'red');
$color-link-focus-alt:          map-get($ui-palette, 'orange');

//  Misc
// ======
$color-subtle:                  $color-off-white;

//  Borders
// =========
//   Default border colours
//   ----------------------
$color-border:                  $color-grey-mid;
$color-border-light:            $color-grey-light;
$color-border-mid:              $color-grey-mid;
$color-border-dark:             $color-grey;
$color-border-input:            $color-text-default;


// ========================================================
