$shadow-position: 60px;

.listing--featured {
    @extend %listing;

    background-color: $color-grey-lighter;
    position: relative;

    &::after {
        background-color: $color-white;
        bottom: 0;
        box-shadow: $shadow;
        content: '';
        height: 50%;
        left: $shadow-position;
        position: absolute;
        right: $shadow-position;
        z-index: -1;
    }

    .listing__content {
        @include respond-max($large-width) {
            padding: $space-and-quarter;
        }
    }
}
