$toggles-container-width: pxToRem(250px);
$toggles-image-width: pxToRem(360px);

.tabbed-content {
    padding-bottom: $space-and-quarter * 2;
    padding-top: ($space-double + $space-third);

    @include respond-min($large-width) {
        padding-bottom: $space-and-quarter * 4;
        padding-top: $space-quadruple + $space-three-quarters;
    }

    .no-js & {
        .tabbed-content__toggle[data-a11y-toggle] {
            display: block;
        }
    }

    .tabbed-content__heading {
        margin-top: 0;
    }

    .tabbed-content__content {
        margin-top: $space;
    }

    .tabbed-content__tab-container {
        display: flex;
        margin-top: $space-double + $space-third;

        @include respond-max($large-width) {
            display: none;
        }
    }

    .tabbed-content__toggles-container {
        border-right: $border-width-small solid $color-border;
        flex: none;
        width: $toggles-container-width;
    }

    .tabbed-content__toggle-content-container {
        width: calc(100% - #{$toggles-container-width});
    }

    .tabbed-content__toggle {
        border: $border-width-small solid $color-white;
        display: block;
        padding: $space $space-and-sixth;
        text-decoration: none;
        transition: background-color $transition-speed-slow ease-in-out;

        &::after {
            background-color: $color-border;
            content: '';
            height: 100%;
            position: absolute;
            right: -$border-width-mid;
            top: 0;
            transition: background-color $transition-speed;
            width: $border-width-small;
        }

        &:hover,
        &:focus,
        &:active {
            background-color: $color-off-white;
        }

        &:focus,
        &:active {
            position: relative;
            z-index: 1;

            &::after {
                content: none;
            }
        }

        &[aria-expanded="true"] {
            border: $border-width-small solid $color-border;
            border-right: $border-width-small solid $color-white;
            position: relative;

            &::after {
                background-color: $color-white;
            }

            .tabbed-content__tab-heading {
                color: $color-black;
                font-weight: $font-weight-semibold;
            }
        }
    }

    .tabbed-content__tab-heading {
        align-items: center;
        color: $color-text-default;
        display: flex;
        font-weight: $font-weight-normal;
        justify-content: space-between;
        margin: 0;

        .icon {
            display: inline-block;
            flex: none;
            margin-left: $space-half;
        }
    }

    .tabbed-content__toggle-content-inner-container {
        padding: 0 ($space-and-quarter * 2);

        @include respond-min($xx-large-width) {
            display: flex;
            padding: 0;
        }
    }

    .tabbed-content__tab-content-container {
        align-items: flex-start;
        display: flex;
        flex-direction: column;
        padding-bottom: $space-three-quarters;

        @include respond-min($mid-width) {
            padding-bottom: $space-and-quarter * 2;
        }

        @include respond-min($xx-large-width) {
            padding: $space-and-quarter * 2;
        }

        @include if-ie(ie) {
            flex: 1;
        }
    }

    .tabbed-content__tab-content {
        margin-bottom: $space;
        max-width: 100%;

        *:first-child {
            margin-top: 0;
        }
    }

    .tabbed-content__tab-images-container {
        flex: none;

        @include respond-min-and-max($small-width, $xx-large-width) {
            display: flex;
            justify-content: space-between;
        }

        @include if-ie(ie) {
            display: block;
        }
    }

    .tabbed-content__image-container {
        + .tabbed-content__image-container {
            margin-top: $space-half;

            @include respond-min($small-width) {
                margin-left: $space-three-quarters;
                margin-top: 0;

                @include if-ie(ie) {
                    margin-left: 0;
                    margin-top: $space-half;
                }
            }

            @include respond-min($xx-large-width) {
                margin-left: 0;
                margin-top: $space-half;
            }
        }
    }

    .tabbed-content__image {
        width: $toggles-image-width;
    }

    .tabbed-content__button {
        margin: auto 0 0;
    }

    .tabbed-content__tab-container--mobile {
        display: block;

        @include respond-min($large-width) {
            display: none;
        }

        .tabbed-content__toggle-content-inner-container {
            padding: $space-three-quarters $space-half;

            @include respond-min($mid-width) {
                padding: $space-and-quarter;
            }
        }

        .tabbed-content__toggle {
            border: $border-width-small solid $color-border;

            &[aria-expanded="true"] {
                border-right: $border-width-small solid $color-border;
            }

            &:not(:first-child) {
                margin-top: -$border-width-small;
            }
        }
    }
}

.tabbed-content--grey {
    background-color: $color-off-white;

    @include respond-min($x-large-width) {
        margin-top: $space-double + $space-two-thirds;
    }

    .tabbed-content__toggles-container {
        background-color: $color-grey;
        border: 0;
    }

    .tabbed-content__toggle-content-container {
        @include respond-min($large-width) {
            order: -1;
        }
    }

    .tabbed-content__tab-heading {
        color: $color-white;
    }

    .tabbed-content__tab-container {
        margin-top: 0;
    }

    .tabbed-content__toggle {
        border: 0;
        border-color: $color-grey;

        &:hover,
        &:focus,
        &:active {
            background-color: $color-grey-dark;
        }

        &[aria-expanded="true"] {
            background-color: $color-grey-dark;
            border-left: 0;
            border-right: 0;

            &::after {
                content: none;
            }

            .tabbed-content__tab-heading {
                color: $color-white;
            }

            &:first-child {
                border-top: 0;
            }
        }
    }

    .tabbed-content__tab-container--mobile {
        .tabbed-content__toggle {
            background-color: $color-grey;
            border-left: 0;
            border-right: 0;

            &[aria-expanded="true"],
            &:hover,
            &:focus,
            &:active {
                background-color: $color-grey-dark;

                &:first-child {
                    border-top: $border-width-small solid $color-off-white;
                }
            }
        }
    }

    .tabbed-content__tab-images-container {
        @include respond-min($large-width) {
            order: -1;
        }
    }
}
