.button--disabled,
%button--disabled,
.button[disabled] {
    &,
    &:visited {
        background-color: lighten($color-disabled, 30%);
        border: $border-width-mid solid $color-disabled;
        color: $color-text-muted;
        cursor: not-allowed;
    }

    &:hover,
    &:focus,
    &:active {
        background-color: lighten($color-disabled, 30%);
        border-color: $color-disabled;
        color: $color-text-muted;
    }
}
