.site-plan {
    .site-plan__image-container {
        margin-top: $space-three-quarters;
    }

    .site-plan__heading {
        margin-top: 0;
    }
}

.site-plan--grey {
    background-color: $color-off-white;
    padding-bottom: $space-and-quarter * 2;
    padding-top: ($space-double + $space-third);

    @include respond-min($large-width) {
        padding-bottom: $space-and-quarter * 4;
        padding-top: $space-quadruple + $space-three-quarters;
    }

    .widget &:last-child,
    .widget-row > *:last-child > *:last-child > &:last-child {
        margin-bottom: -($space-double + $space-third);

        @include respond-min($large-width) {
            margin-bottom: -($space-quadruple + $space-three-quarters);
        }
    }
}
