.button--warning,
%button--warning {
    &,
    &:visited {
        background-color: $color-warning;
        border: $border-width-mid solid $color-black;
        color: $color-black;
    }

    &:hover,
    &:focus,
    &:active {
        background-color: lighten($color-warning, 5%);
        color: $color-black;
    }
}
