.button--success,
%button--success {
    &,
    &:visited {
        background-color: $color-success;
        border: $border-width-mid solid $color-black;
        color: $color-black;
    }

    &:hover,
    &:focus,
    &:active {
        background-color: lighten($color-success, 5%);
        color: $color-black;
    }
}
