.widget-carousel {
    background-color: $color-grey;
    background-image: url('../dist/images/taupe-background.svg');
    background-position: right top;
    background-repeat: no-repeat;
    background-size: percentage(9 / 10);
    padding: ($space-double + $space-twelfth) 0 ($space-and-quarter * 2);

    @include respond-min($large-width) {
        padding: ($space-and-third + $space-and-third) 0 ($space-quadruple + $space);
    }

    @include respond-min($full-width) {
        background-size: auto;
    }

    .widget-carousel__meta {
        @extend %micro-meta;

        color: $color-white;
        margin: 0 0 ($space-hard * 2);

        @include respond-min($large-width) {
            margin-left: $space-and-quarter;
        }
    }

    .flickity-viewport {
        background-color: $color-white;
    }

    .widget-carousel__image-container {
        @include respond-min($x-large-width) {
            width: 50%;
        }
    }

    .widget-carousel__content {
        @include respond-max($large-width) {
            padding: $space-hard * 2;
        }
    }

    .widget-carousel__heading {
        @extend %h2;
    }

    .widget-carousel__subheading {
        @extend %subheading;
        @extend %zeta;

        display: block;
        margin: $border-width-large 0 0;

        @include respond-max($large-width) {
            font-size: $theta;
        }
    }

    .flickity-page-dots-wrapper {
        bottom: -$space-double;
    }

    .flickity-buttons-wrapper {
        @include respond-max($full-width) {
            bottom: -$space-and-third;
            top: auto;

            .flickity-prev-next-button.next {
                right: 0;
            }

            .flickity-prev-next-button.previous {
                left: 0;
            }
        }

        .flickity-prev-next-button {
            @include respond-min-and-max($mid-width, $large-width) {
                height: $carousel-button-size-small;
                width: $carousel-button-size-small;
            }

            &.next {
                @include respond-min($full-width) {
                    right: -($space-double + $space-half);
                }
            }

            &.previous {
                @include respond-min($full-width) {
                    left: -($space-double + $space-half);
                }
            }
        }
    }
}
