.button--danger,
%button--danger {
    &,
    &:visited {
        background-color: $color-danger;
        border: $border-width-mid solid $color-black;
        color: $color-black;
    }

    &:hover,
    &:focus,
    &:active {
        background-color: lighten($color-danger, 5%);
        color: $color-black;
    }
}
