// Navigation list items with arrows
%list--navigation,
.list--navigation {
    margin-bottom: $slug-large;

    .list__item {
        padding-left: $gutter-large;
        position: relative;

        &::before {
            background-position: left;
            background-size: 60%;
            content: '';
            height: $space-hard * 2;
            left: 0;
            position: absolute;
            width: $space;
        }
    }

    .list__link {
        display: inline;
    }
}
