.icons {
    margin-top: $space-and-two-thirds * 2;
    position: relative;
    z-index: 1;

    @include respond-min($large-width) {
        margin-top: ($space-and-quarter * 2) + $space-sixth;
    }

    &::before {
        @include respond-max($large-width) {
            background-color: $color-border;
            content: '';
            height: $border-width-small;
            position: absolute;
            top: -($space-triple + $space-sixth);
            width: 100%;
        }
    }

    .icons__container {
        text-align: center;

        @include respond-min($large-width) {
            display: flex;
        }
    }

    .icons__item {
        @include respond-min($large-width) {
            width: calc(#{percentage(1 / 3)} - #{$space-quadruple / 3});

            &:nth-child(2) {
                margin: 0 $space-double;
            }
        }

        @include respond-max($large-width) {
            &:not(:first-child) {
                margin-top: $space-double;
            }
        }
    }

    .icons__icon {
        margin: $space-two-thirds auto $space-and-sixth;
        position: relative;

        @include respond-min($large-width) {
            margin: $space-two-thirds auto $space-and-sixth;
        }

        &::before {
            border: $border-width-mid solid $color-primary;
            border-radius: 50%;
            bottom: 0;
            content: '';
            display: block;
            height: $space-triple;
            left: 50%;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
            width: $space-triple;
        }
    }

    .icons__heading {
        font-family: $font-family;
        font-size: $eta;
        font-weight: $font-weight-normal;
        letter-spacing: initial;
        text-transform: initial;
    }

    .icons__content {
        margin: $space-quarter auto 0;
        max-width: $small-mid-width;
    }
}

.icons--contained {
    @include respond-min($large-width) {
        margin-top: -$space-third;
    }

    @include respond-max($large-width) {
        border-bottom: $border-width-small solid $color-border;
        padding-bottom: $space-double + $space-third;
    }

    &::before {
        @include respond-max($large-width) {
            top: -($space-and-quarter * 2);
        }
    }

    .icons__container {
        @include respond-min($large-width) {
            float: right;
            width: percentage(4 / 7);
        }
    }

    .icons__icon {
        height: $space-double + $space-twelfth;
        margin: 0 auto $space-two-thirds;
        width: $space-double + $space-twelfth;

        &.icon-toptasks-shared-ownership-and-self-build {
            width: $space-and-two-thirds;
        }

        &::before {
            content: none;
        }
    }

    .icons__heading {
        font-weight: $font-weight-semibold;
    }
}

.icons--minus-margin {
    @include respond-min($large-width) {
        margin-top: 0;
    }
}

.icons--has-border {
    .container {
        @include respond-max($large-width) {
            border-bottom: $border-width-small solid $color-border;
            padding-bottom: $space-and-two-thirds + $space-two-thirds;
        }
    }

    .icons__container {
        @include respond-min($large-width) {
            border-bottom: $border-width-small solid $color-border;
            padding-bottom: $space-quadruple + $space-two-thirds;
        }
    }
}
