// ========================================================
//  Site Footer
// ==============

$footer-background-color: $color-black !default;
$footer-york-logo-height: pxToRem(90px);

.site-footer {
    @extend %module;

    background-color: $footer-background-color;
    background-position: right top;
    background-repeat: no-repeat;
    color: $color-white;
    margin: $space-double 0 0;
    padding: $space-and-sixth 0 0;

    @include respond-min($large-width) {
        background-image: url('../dist/images/footer-background.svg');
        padding: ($space-quadruple + $space-two-thirds) 0 0;
    }

    .container {
        max-width: $container-standard;
    }

    a:not(.button) {
        &,
        &:visited {
            color: $color-white;
            text-decoration: none;
        }

        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;
        }
    }

    .button {
        border: $border-width-small solid $color-white;
    }

    .home-page &,
    .homepage-modular &,
    .index-modular & {
        margin-top: 0;
    }
}

.site-footer__register {
    text-align: left;

    .site-footer__text {
        display: block;
        font-size: $normalis;

        @include respond-max($large-width) {
            font-size: $milli;
        }
    }

    .button {
        margin: $space-and-two-thirds 0 0;
    }
}

.site-footer__col {
    @include respond-min($large-width) {
        float: left;
        width: percentage(3 / 5);

        &:first-child {
            padding-right: $space;
            width: percentage(2 / 5);
        }
    }
}

.site-footer__heading {
    color: $color-white;
    margin-top: $space-sixth;
}

.site-footer__subheading {
    @extend %subheading;

    color: $color-white;
    display: block;
    font-size: $zeta;
    margin-top: $space-twelfth;

    @include respond-max($large-width) {
        font-size: $iota;
    }
}

.site-footer__logo--ashtons {
    margin: $space-and-sixth 0;

    @include respond-min($large-width) {
        margin-bottom: 0;
    }

    @include respond-max($large-width) {
        width: pxToRem(100px);
    }
}

.identity--footer-desktop {
    @include respond-max($large-width) {
        display: none;
    }
}

.identity--footer-mobile {
    @include respond-min($large-width) {
        display: none;
    }

    @include respond-max($mid-width) {
        .identity__logo {
            width: pxToRem(103px);
        }
    }
}

.identity__link {
    display: inline-block;
}

.site-footer__logos {
    margin-top: $space-quadruple;

    .site-footer__col {
        &:last-child {
            align-items: center;
            display: flex;
            position: relative;

            @include respond-max($large-width) {
                flex-wrap: wrap;
                justify-content: space-between;
                margin-bottom: $space-and-third;

                &::before,
                &::after {
                    background-color: $color-black-light;
                    content: '';
                    height: $border-width-small;
                    left: 0;
                    margin-left: -$space-and-quarter;
                    position: absolute;
                    top: -$space-double;
                    width: calc(100% + #{$space-and-quarter * 2});
                }

                &::after {
                    bottom: -$space-double;
                    top: auto;
                }
            }

            @include respond-min-and-max($large-width, $xx-large-width) {
                flex-wrap: wrap;
            }
        }
    }
}

.site-footer__logo-link {
    display: block;

    &:hover,
    &:focus,
    &:active {
        .site-footer__logo {
            opacity: .7;
        }
    }
}

.site-footer__logo-text {
    @extend %subheading;

    color: $color-white;
    flex-grow: 1;
    font-size: $milli;
    order: -1;
    width: 50%;

    @include respond-min($large-width) {
        font-size: $zeta;
        margin: 0 $space;
        order: 0;
        width: auto;
    }

    @include respond-max($large-width) {
        border-bottom: $border-width-small solid $color-black-light;
        margin-bottom: $space-double;
        margin-left: -$space-and-quarter;
        min-height: $footer-york-logo-height;
        padding-left: $space-and-quarter;
        width: calc(50% + #{$space-and-quarter});
    }

    @include respond-min-and-max($large-width, $xx-large-width) {
        margin: 0;
        width: 50%;
    }
}

.site-footer__logo-link--nhbc {
    margin-left: $space-double;

    @include respond-max($large-width) {
        margin-left: 0;
    }

    @include respond-min-and-max($large-width, $xx-large-width) {
        margin-top: $space-and-third;
    }

    .site-footer__logo {
        @include respond-max($mid-width) {
            width: pxToRem(78px);
        }
    }
}

.site-footer__york-logo-container {
    @include respond-max($large-width) {
        border-bottom: $border-width-small solid $color-black-light;
        margin-bottom: $space-double;
        margin-right: -$space-and-quarter;
        min-height: $footer-york-logo-height;
        padding-right: $space-and-quarter;
        width: calc(50% + #{$space-and-quarter});
    }

    @include respond-min-and-max($large-width, $xx-large-width) {
        width: 50%;
    }
}

.site-footer__logo-link--york {
    float: left;

    @include respond-max($large-width) {
        float: right;
    }

    .site-footer__logo {
        @include respond-max($large-width) {
            width: pxToRem(100px);
        }
    }
}

.site-footer__logo-link--consumercode {
    @include respond-min-and-max($large-width, $xx-large-width) {
        margin-top: $space-and-third;
    }

    .site-footer__logo {
        @include respond-max($mid-width) {
            width: pxToRem(70px);
        }
    }
}

.site-footer__suppliers {
    padding: $space-and-sixth 0 $space-half;

    @include respond-min($large-width) {
        padding: ($space-quadruple) 0 $space-half;
    }

    .site-footer__text {
        display: inline;

        &:first-child {
            margin-right: $space;
        }
    }
}

.site-footer__bottom {
    background-color: $color-black-light;

    .container {
        padding-bottom: $space;
        padding-top: $space;

        @include respond-min($large-width) {
            align-items: center;
            display: flex;
        }

        @include respond-min-and-max($large-width, $xx-large-width) {
            flex-wrap: wrap;
        }
    }
}

.site-footer__bottom-heading {
    @extend %subheading;

    color: $color-white;
    display: block;
    line-height: 1.2;
    margin: 0 auto $space-two-thirds 0;

    @include respond-min-and-max($large-width, $xx-large-width) {
        flex: none;
        width: 100%;
    }

    @include respond-min($xx-large-width) {
        margin-bottom: 0;
    }
}

.site-footer__links {
    .list__item {
        &,
        &:last-child {
            margin: 0 ($space-double + $space-hard) 0 0;

            @include respond-max($large-width) {
                display: block;
                margin-bottom: $space-two-thirds;
            }
        }
    }

    .list__link {
        font-size: $milli;
        line-height: 1;
    }
}

.site-footer__text,
.site-footer__links {
    display: block;
    font-size: $micro;
    margin: 0;

    @include respond-min($mid-width) {
        display: inline;
    }
}

.site-footer__link {
    font-weight: $font-weight-bold;
}

.site-footer__rights {
    font-size: $milli;
    font-weight: $font-weight-normal;

    @include respond-min-and-max($large-width, $xx-large-width) {
        margin-left: auto;
    }

    @include respond-min($x-large-width) {
        line-height: 1;
    }
}
