$banner-image-height:           pxToRem(769px);
$banner-image-height-small:     pxToRem(500px);
$banner-content-width:          pxToRem(840px);

.banner {
    position: relative;

    .banner__image-container {
        height: $banner-image-height-small;
        overflow: hidden;
        position: relative;

        @include respond-min($large-width) {
            height: $banner-image-height;
        }

        &.compat-object-fit {
            background-position: center center;
            background-size: cover;

            .banner__image {
                opacity: 0;
            }
        }
    }

    .banner__image {
        height: 100%;
        object-fit: cover;
        width: 100%;

        @include respond-min($large-width) {
            height: 108%; // increased for parallax
        }
    }

    .container {
        padding-bottom: $space-and-two-thirds + $space-two-thirds;
        padding-top: $space-and-two-thirds + $space-two-thirds;

        @include respond-min($large-width) {
            bottom: 0;
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
        }
    }

    .banner__content-container {
        @include respond-min($large-width) {
            background-color: $color-white;
            max-width: $banner-content-width;
            padding: (($space-and-quarter * 4) - $space-quarter) ($space-and-quarter * 4);
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 100%;
        }
    }

    .banner__heading {
        margin-top: 0;
    }

    .banner__content {
        margin-top: $space;
    }

    .banner__item {
        margin-right: $space-double;
        margin-top: $space-and-sixth;
        text-align: center;

        @include respond-min($mid-width) {
            display: inline-block;
        }
    }

    .banner__icon {
        margin: 0 auto;
    }

    .icon-toptasks-house-types,
    .icon-toptasks-shared-ownership,
    .icon-toptasks-self-build {
        height: $space-double + $space-twelfth;
        width: $space-double + $space-twelfth;
    }

    .banner__icon-heading {
        font-family: $font-family;
        font-size: $eta;
        font-weight: $font-weight-semibold;
        letter-spacing: initial;
        text-transform: initial;
    }

    .banner__icon-content {
        margin-top: $space-half;
    }
}
