%listing--compact,
.listing--compact {
    .listing__heading {
        @extend %zeta;

        margin-top: 0;
    }

    .listing__summary {
        @extend %milli;

        margin-bottom: $space-half;
    }

    .listing__meta {
        @extend %milli;
    }

    &:not(.listing--featured) {
        .listing__content {
            padding: 0;
        }
    }
}
