// ========================================================
//  Type
// ========================================================
// Apply various font sizes by class. Pragmatic & practical font sizing
// as per: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/

// Type sizes
// ========================================================
//  Oversized Type Range
// ======================
%giga,
.giga {
    font-size: $giga;
    margin: $space-half 0;
}

%mega,
.mega {
    font-size: $mega;
    margin: $space-half 0;
}

%kilo,
.kilo {
    font-size: $kilo;
    line-height: 1.2;
}

//  Standard Type Range
// =====================
%alpha,
.alpha {
    font-size: $alpha;
    line-height: 1.2;
    margin: $space-half 0 0;
}

%beta,
.beta {
    font-size: $beta;
    line-height: 1.3;
    margin: $space-two-thirds 0 $space-half;
}

%gamma,
.gamma {
    font-size: $gamma;
    line-height: 1.3;
    margin: $space 0 0;
}

%delta,
.delta {
    font-size: $delta;
    line-height: 1.2;
    margin: $space-three-quarters 0 0;
}

%epsilon,
.epsilon {
    font-size: $epsilon;
    margin: $space-third 0 0;
}

%zeta,
.zeta {
    font-size: $zeta;
    margin: $space-third 0 0;
}

%eta,
.eta {
    font-size: $eta;
    line-height: 1.3;
    margin: $space-third 0 0;
}

%theta,
.theta {
    font-size: $theta;
    line-height: 1.3;
    margin: $space-third 0 0;
}

%iota,
.iota {
    font-size: $iota;
    line-height: 1.3;
    margin: $space-third 0 0;
}

//  Base Type Size
// ================
%normalis,
.normalis {
    font-size: $normalis;
    margin: $space-half 0 0;
}

//  Base Type Reset
// =================
%type-reset {
    font-family: $font-family;
    font-size: $normalis;
    font-style: normal;
    font-weight: $font-weight-normal;
    line-height: $line-height-ratio;
}

//  Small Type Range
// ==================
%milli,
.milli {
    font-size: $milli;
    margin: $space-half 0 0;
}

%micro,
.micro {
    font-size: $micro;
    margin: $space-two-thirds 0 0;
}

// ========================================================


//  Headings
// ========================================================
//  Heading styling. All headings are also available as
//  classes. Eg: .h1, .h2 etc…
//  Ref: http://csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/
h1,
h2,
h3,
h4,
h5,
h6 {
    color: $color-black;
    font-weight: $font-weight-heading;
}

h1 {
    @extend %alpha;

    font-family: $font-family-gilroy;
    font-weight: $font-weight-normal;
    letter-spacing: $border-width-mid;
    margin-top: 0;
    text-transform: uppercase;

    @include respond-max($large-width) {
        font-size: $beta;
    }

    + .subheading {
        @extend %gamma;

        display: block;
        margin: 0;
    }
}

h2 {
    @extend %delta;

    font-family: $font-family-gilroy;
    font-weight: $font-weight-normal;
    letter-spacing: $border-width-mid;
    text-transform: uppercase;

    @include respond-max($large-width) {
        font-size: $epsilon;
    }

    + .subheading {
        @extend %zeta;

        display: block;
        margin: $border-width-large 0 0;

        @include respond-max($large-width) {
            font-size: $theta;
        }
    }
}

h3 {
    @extend %eta;

    @include respond-max($large-width) {
        font-size: $theta;
    }
}

h4 {
    @extend %theta;

    @include respond-max($large-width) {
        font-size: $iota;
    }
}

h5 {
    @extend %iota;

    @include respond-max($large-width) {
        font-size: $normalis;
    }
}

h6 {
    @extend %normalis;
}

h2 + h3 {
    margin-top: 0;
}

h3 + h4 {
    margin-top: $space-quarter;
}

h5 + h6 {
    margin-top: $space-quarter;
}

@for $i from 1 through 6 {
    %h#{$i},
    .h#{$i} {
        @extend h#{$i};

        display: block;
    }
}

.subheading,
%subheading {
    color: $color-text-default;
    font-family: $font-family-merriweather;
    font-style: italic;
    font-weight: $font-weight-light;

    &.h2 {
        letter-spacing: normal;
        text-transform: initial;
    }
}

// ========================================================

//  Summary
// ========================================================
%summary,
.summary {
    color: $color-black;
    font-size: $iota;
    font-weight: $font-weight-normal;
}

%micro-meta {
    font-size: $micro;
    font-weight: $font-weight-normal;
    letter-spacing: $border-width-mid;
    text-transform: uppercase;
}

p {
    font-weight: $font-weight-light;
    line-height: 1.7;
}
