$image-height-small:     pxToRem(206px);
$image-height-mid:       pxToRem(300px);
$image-height-large:     pxToRem(433px);
$image-width-large:      pxToRem(540px);

%listing,
.listing {
    position: relative;

    @include respond-min($mid-width) {
        display: flex;
    }

    .listing__image-container {
        @include respond-min($xx-small-width) {
            min-height: $image-height-small;
        }

        @include respond-min($small-width) {
            min-height: $image-height-mid;
        }

        @include respond-min($mid-width) {
            min-height: $image-height-large;
            width: 50%;
        }

        @include respond-min($x-large-width) {
            width: $image-width-large;
        }

        &.compat-object-fit {
            background-position: center center;
            background-size: cover;

            .listing__image {
                opacity: 0;
            }
        }

        .listing__image {
            height: 100%;
            object-fit: cover;
            width: 100%;
        }
    }

    .listing__content {
        padding-top: $space-and-quarter;

        @include respond-min($mid-width) {
            align-items: flex-start;
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            padding: $space-and-quarter;
            width: 50%;

            @include if-ie(ie) {
                * {
                    max-width: 100%;
                }
            }
        }

        @include respond-min($large-width) {
            padding: $space-double;
        }
    }

    .listing__heading {
        margin-top: 0;
    }

    .meta--date {
        @extend %micro-meta;

        margin-top: $space-third;

        .meta__label {
            @include visuallyhidden;
        }
    }

    .listing__summary {
        margin-top: $space-two-thirds;
        max-width: 100%;

        @include respond-min($large-width) {
            margin-top: $space-double;
        }
    }

    .listing__button {
        margin: 0;

        @include respond-min($mid-width) {
            margin-top: auto;
        }

        &::before {
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
        }

        &:focus,
        &:active {
            box-shadow: none;

            &::before {
                box-shadow: inset 0 0 0 $border-width-large $color-link-focus;
            }
        }
    }
}

.listing-meta {
    @extend %micro-meta;

    color: $color-black;
    display: block;
    line-height: 1;
    margin-bottom: $space-and-quarter;

    @include respond-min($large-width) {
        margin-left: $space-and-quarter;
    }
}
