.button--standout,
%button--standout {
    &,
    &:visited {
        background-color: transparent;
        border-color: $color-white;
        border-width: $border-width-mid;
        color: $color-white;
    }

    &:hover,
    &:focus,
    &:active {
        background-color: $color-white;
        color: $color-text-default;
    }
}
