$pseudo-background-size:     pxToRem(661px);
$pseudo-background-1-top:    pxToRem(483px);
$pseudo-background-1-right:  pxToRem(360px);
$pseudo-background-2-bottom: pxToRem(478px);
$pseudo-background-3-left:   pxToRem(201px);

.content-with-links {
    position: relative;
    text-align: center;

    &::before {
        background: url('../dist/images/light-background.svg') no-repeat;
        background-size: cover;
        content: '';
        display: block;
        height: $pseudo-background-size;
        position: absolute;
        right: -$pseudo-background-1-right;
        top: -$pseudo-background-1-top;
        width: $pseudo-background-size;
        z-index: -1;

        @include respond-max($large-width) {
            height: $pseudo-background-size / 2;
            right: -($pseudo-background-1-right / 2);
            top: -($pseudo-background-1-top / 2);
            width: $pseudo-background-size / 2;
        }
    }

    .content-with-links__container {
        overflow: hidden;
        padding-bottom: $space-and-quarter * 2;
        padding-top: ($space-double + $space-third);
        position: relative;

        @include respond-min($large-width) {
            padding-bottom: $space-and-quarter * 4;
            padding-top: $space-quadruple + $space-three-quarters;
        }

        &::before {
            background: url('../dist/images/light-background.svg') no-repeat;
            background-size: cover;
            bottom: -$pseudo-background-2-bottom;
            content: '';
            display: block;
            height: $pseudo-background-size;
            left: -$pseudo-background-3-left;
            position: absolute;
            width: $pseudo-background-size;

            @include respond-max($large-width) {
                bottom: -($pseudo-background-2-bottom / 2);
                height: $pseudo-background-size / 2;
                left: -($pseudo-background-3-left / 2);
                width: $pseudo-background-size / 2;
            }
        }
    }

    .container {
        max-width: pxToRem(720px);
        position: relative;
    }

    .content-with-links__heading {
        margin-top: -$border-width-large;
    }

    .content-with-links__content {
        margin-top: $space;
    }

    .button {
        margin: $space $space-half 0;

        &:nth-child(2) {
            @extend %button--secondary;
        }
    }
}
