$content-container-width: $full-width - 60px;

.content-custom {
    .hero-first & {
        .container {
            background-color: $color-white;

            @include respond-min($full-width) {
                margin-top: -($space-and-quarter * 2);
                position: relative;
                z-index: 1;
            }
        }
    }

    .container {
        padding-bottom: ($space-double + $space-third);
        padding-top: ($space-double + $space-third);

        @include respond-min($large-width) {
            display: flex;
        }

        @include respond-min($full-width) {
            max-width: $content-container-width;
            padding: ($space-double + $space-third) ($space-and-quarter * 2);
        }
    }

    .content-custom__col {
        @include respond-min($large-width) {
            align-items: flex-start;
            display: flex;
            flex-direction: column;
            padding-right: $space-double;
            width: percentage(3 / 7);
        }

        &:last-child {
            margin-top: $space;

            @include respond-min($large-width) {
                margin-top: 0;
                padding: 0;
                width: percentage(4 / 7);
            }
        }
    }

    .highlight-intro {
        p:first-child {
            @extend %summary;
        }
    }

    .content-custom__subheading {
        &.margin-bottom {
            margin-bottom: $space;
        }
    }

    .content-custom__text {
        p,
        p:first-child,
        li {
            font-size: $eta;
        }
    }

    .link-follows {
        > *:last-child {
            margin-bottom: $space;

            @include respond-min($large-width) {
                margin-bottom: $space-and-half;
            }
        }
    }

    .content-custom__button {
        margin: auto 0 0;
    }
}
