// Widgets
//
// Orbit provides mapping between widget sizes and the susy grid.
// Note, widgets use a custom 100 col grid rather than the default 12 col grid to allow for correct widget sizes.
// You can view an example widget page at /widgets.html to see how they work with the grid.
// Feel free to change as needed (use caution though!).
// See sass/layout/_widgets.scss.
//
// Styleguide 19.5

.widget-row {
    @extend %cf;
    @extend %module;

    margin-bottom: $space-double + $space-third;

    @include respond-min($large-width) {
        margin-left: -$gutter-small;
        margin-right: -$gutter-small;
    }

    @include respond-min($large-width) {
        margin-bottom: $space-quadruple + $space-three-quarters;
    }

    &:first-of-type {
        margin-top: 0;
    }
}

.widget-row--flush {
    margin-bottom: 0;
}

.widget-width {
    @extend %cf;

    float: left;
    width: 100%;

    @include respond-min($large-width) {
        padding: 0 $gutter-small;
    }

    @include respond-max($large-width) {
        margin-bottom: $space-double + $space-third;
    }
}

@include respond-min($large-width) {
    @include widget-width;
}

.widget-stack {
    .widget:not(:last-child) {
        margin-bottom: $space;
    }
}

.widget {
    @extend %module;
    @extend %cf;
}

.widget-inner {
    @extend %boxed;
}

%widget__heading,
.widget__heading {
    font-size: $delta;
    margin-bottom: $slug;

    &:first-child {
        margin-top: 0;
    }
}

%widget__subheading,
.widget__subheading {
    font-size: $zeta;
}
