// Toggled menu
$menu-background-color: $color-black !default;

.menu-navigation {
    @include toggleFade();

    background-color: $menu-background-color;
    left: 0;
    overflow-y: auto;
    padding: $space-double $space-and-half 0;
    top: 100%;
    width: 100%;

    .no-js & {
        display: block;
        position: static;
        transform: translateY(0);
    }

    .container {
        height: 100%;

        @include respond-max($full-width) {
            padding: 0 $space;
        }
    }

    .nav__heading {
        font-size: $delta;
        font-weight: $font-weight-heading;
        margin: 0 0 $space-two-thirds;
    }

    .list__link {
        &,
        &:visited {
            color: $color-white;
            text-decoration: none;
        }

        &:hover,
        &:focus,
        &:active {
            color: $color-tertiary;
        }
    }

    .nav {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    .menu-navigation__primary-list {
        flex-grow: 1;

        .list__item {
            margin: $space-and-sixth 0;

            &:first-child {
                margin-top: $border-width-large; // match design & fix outline
            }
        }

        .list__link {
            font-family: $font-family-gilroy;
            font-size: $epsilon;
            letter-spacing: $border-width-mid;
            text-transform: uppercase;
        }
    }

    .menu-navigation__secondary-list {
        padding-bottom: $space-and-half;

        .list__link {
            font-size: $milli;
        }
    }
}
