// ========================================================
//  Item Lists
// ========================================================

//  Base style
// ============
$list-item-spacing: $space-hard !default;
$list-line-height: 1.3 !default;

%list--child {
    margin-left: $gutter-large;
}


.list,
%list {
    line-height: $list-line-height;
    list-style: none;
    margin-left: 0;
    margin-top: 0;

    time {
        display: block;
        font-size: $milli;
        margin: $space-sixth 0 $slug;
    }
}

.list__item,
%list__item {
    display: block;
    margin: $list-item-spacing 0;
}

.list__link,
%list__link {
}

.list__link--disabled {
    color: $color-text-default;
}
