// Links
//
// Base link styling
// See sass/objects/_link.scss.
//
// :hover       - Hover state.
// :active      - Active state.
// :focus       - Focus state.
//
// Markup:
// <a class="{$modifiers}">Link</a>
//
// Styleguide 11

a {
    color: $color-link;
    transition: $transition-speed-slow color ease-in-out;

    &,
    &:visited {
        text-decoration: underline;
    }

    &:visited:not(.button) {
        color: $color-link-visited;

        &:hover {
            color: $color-link-hover;
        }
    }

    &:hover,
    &:focus,
    &:active {
        color: $color-link-hover;
    }

    &:focus {
        @include focus-outline;
    }
}

.link-external {
    display: inline-block;

    &::after {
        background-size: cover;
        content: '';
        display: inline-block;
        height: $space-half;
        margin-left: $space-quarter;
        width: $space-half;
    }
}
