.widget-gallery {
    .image {
        float: none;
        margin: auto;
        max-width: none;
        width: 100%;
    }

    .image__caption:empty {
        display: none;
    }

    .flickity-viewport {
        &::before {
            background-image: $gradient;
            bottom: 0;
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 1;
        }
    }
}
