.site-search {
    .form__control {
        margin: 0;
    }
}

.site-search--header {
    background-color: $color-grey-light;
    color: $color-primary;
    display: none;
    left: 0;
    margin: 0;
    padding: $space-and-third 0;
    position: absolute;
    top: 0;
    transform: translateY(-100%);
    transition: opacity $transition-speed ease-in-out;
    width: 100%;
    z-index: 0;

    @include placeholder() {
        color: $color-primary;
        text-transform: uppercase;
    }

    .no-js & {
        display: block;
        margin-top: $slug;
        position: static;
        transform: translateY(0);
    }

    &[aria-hidden='false'],
    &.is-open[aria-hidden='true'] {
        display: block;
        opacity: 0;
    }

    .form__control {
        width: 100%;

        // Animated pseudo element styled as a border
        &::after {
            background-color: $color-primary;
            bottom: 0;
            content: '';
            height: $border-width-large;
            left: 0;
            opacity: 0;
            position: absolute;
            transform: scaleX(0);
            transform-origin: left;
            transition: transform .5s ease-out $transition-speed, opacity 1s ease-out $transition-speed-slow;
            width: 100%;

            .no-js & {
                opacity: 1;
                transform: scaleX(1);
            }
        }
    }

    &.is-open {
        opacity: 1;

        .form__control::after {
            opacity: 1;
            transform: scaleX(1);
        }

        .button {
            opacity: 1;
        }
    }

    .form__append-group {
        margin-bottom: $space-quarter;
    }

    .form__field {
        background-color: transparent;
        border: 0;
        font-size: $gamma;
        margin: 0 $gutter-small 0 0;
        padding: $space-half 0;
        position: relative;
        transition: padding $transition-speed ease-out;

        @include placeholder() {
            color: $color-primary;
            text-transform: uppercase;
        }

        &:focus,
        &:active {
            outline-color: $color-link-focus-alt;
            padding: $space-half;
        }
    }

    .button {
        background-color: transparent;
        border: 0;
        margin: 0;
        opacity: 0;
        padding: 0 0 0 $space-quarter;
        transition: opacity $transition-speed-slow ease-out .6s, padding $transition-speed ease-out;

        .no-js & {
            opacity: 1;
        }

        &:hover,
        &:focus,
        &:active {
            background-color: transparent;
        }

        &:focus,
        &:active {
            outline-color: $color-link-focus-alt;
            padding: 0 $space-quarter;
        }
    }

    .icon {
        display: inline-block;
    }

    .icon-fallback {
        @include visuallyhidden;
    }

    .site-search__focus-button {
        display: block;
    }
}
