.identity {
    align-self: center;
    flex: 0 1 auto;
}

.identity__logo {
    transition: opacity $transition-speed-fast ease-in-out;
    width: pxToRem(102px);

    @include respond-min($small-width) {
        width: pxToRem(138px);
    }
}

.identity__link {
    display: block;

    &:hover,
    &:focus,
    &:active {
        .identity__logo {
            opacity: .7;
        }
    }
}
